
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import XProgress from 'vux/src/components/x-progress/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import api from '../api/api.js'
import Vue from 'vue'
const imgList = [
`${api.imgUrl}dy-tu-01.jpg`]
export default {
components: {
Swiper,
SwiperItem,
XProgress,
XDialog,
Checker,
CheckerItem,
Group,
Cell
},
data () {
return {
imgList: imgList,
isParse: true,
yytime: 0,
choose_language: false,
languages: [],
languageChoose: null,
languageDefault: null,
poiId: '',
distance: '',
soundTypes: [],
poiData: {},
lbsPoiData: [],
yycannot: true,
adSrc: '',
spotId: '',
validate: false,
lcsCode: '',
lcstext: '亲，直接复制含有激活码的短信至激活码验证框内，系统可以自动识别哦!',
ison: false,
lcserr: false,
fileUrl: api.fileUrl,
status: 0,
imurl: api.imgUrl
}
},
methods: {
goGuide () {
this.$router.push({path: '/guide', query: {spotId: this.spotId}})
},
getAccessToken () {
let vm = this
let at = localStorage.getItem(vm.spotId)
if (at) {
at = JSON.parse(at)
let params = {spotId: vm.spotId, accessToken: at.accessToken}
api.GetAccessToken(params).then(res => {
if (res.data.status === 1) {
vm.status = 1
}
if (res.data.status === 2) {
localStorage.removeItem(vm.spotId)
vm.status = 0
}
if (res.data.status === 0) {
localStorage.removeItem(vm.spotId)
vm.status = 0
}
console.log(vm.status)
})
} else {
vm.status = 0
}
},
lcsTest: function () {
let vm = this
let lcs = vm.lcsCode.replace(/(^\s*)|(\s*$)/g, '')
let params = {spotId: vm.spotId, licenseCode: lcs}
if (lcs === '') {
vm.lcstext = '请填写激活码'
vm.lcserr = true
return
}
api.GetAccessToken(params).then(res => {
if (res.data.status === 1) {
vm.lcstext = '恭喜您，验证成功！'
let obj = {'accessToken': res.data.accessToken}
let str = JSON.stringify(obj)
localStorage.setItem(vm.spotId, str)
vm.status = 1
vm.lcserr = true
setTimeout(() => {
vm.validate = false
}, 800)
}
if (res.data.status === 0) {
vm.lcserr = true
vm.lcstext = '很抱歉，验证失败。请仔细检查验证码'
}
if (res.data.status === 2) {
vm.lcserr = true
vm.lcstext = '激活设备已超出限制，请重新激活'
localStorage.removeItem(vm.spotId)
}
}, err => {
vm.lcserr = true
vm.lcstext = '很抱歉，验证失败。请仔细检查验证码'
console.log(err)
})
},
goOrder: function () {
this.$router.push({path: '/order', query: {spotId: this.spotId}})
},
yyPlay: function () {
let vm = this

if (vm.cannot) {
return
}
if (vm.status === 0 && vm.poiData.charge === 1) {
vm.validate = true
return
}
var audio = document.getElementById('audio1')
if (audio.paused) {
this.isParse = false
audio.play()
} else {
this.isParse = true
audio.pause()
}
let yytime = 0
let that = this
audio.addEventListener('timeupdate', function () {
let scales = audio.currentTime / audio.duration
yytime = parseInt(scales * 100)
that.yytime = yytime
if (audio.currentTime === audio.duration) {
that.isParse = true
}
}, false)
},
chooseThis: function (value) {
this.choose_language = false
},
goQj: function () {
if (this.status === 0 && this.poiData.charge === 1) {
this.validate = true
return
}
window.open(this.poiData.panourl)
},
getPoiId () {
this.poiId = this.$route.query.poiId
this.distance = this.$route.query.distance
},
getPoidata () {
let vm = this
let params = {id: vm.poiId}
vm.lbsPoiData = []
vm.languages = []
api.GetPoiDetail(params).then(res => {
vm.languages = vm.languages.concat(res.data.soundTypes)
vm.languages.forEach(function (item) {
if (item.sysdefault === 1) {
vm.languageDefault = item
}
})
vm.languageChoose = vm.languageDefault.soundname
vm.poiData = res.data.poiData
vm.spotId = res.data.poiData.spotid
vm.lbsPoiData = vm.lbsPoiData.concat(res.data.lbsPoiData)
console.log(res.data)
Vue.nextTick(function () {
vm.getAdSrc()
vm.getAccessToken()
})
})
},
getAdSrc () {
let vm = this

vm.adSrc = ''
if (vm.poiData.audios.length === 0) {
vm.yycannot = true
} else {
let language = vm.languageChoose
vm.poiData.audios.forEach(function (audio) {
if (audio.soundname === language) {
vm.adSrc = api.fileUrl + audio.url
vm.yycannot = false
}
})
if (vm.adSrc === '') {
vm.yycannot = true
}
}
},
changePoi (id) {
this.$router.push({path: '/scenicArea', query: {poiId: id}})
this.poiId = id
this.getPoidata()
this.isParse = true
let audio = document.getElementById('audio1')
audio.pause()
this.yytime = 0
}
},
beforeCreated () {
},
created: function () {
this.getPoiId()
},
mounted: function () {
this.getPoidata()
},
watch: {
languageChoose: function () {
this.getAdSrc()
this.yytime = 0
this.isParse = true
let audio = document.getElementById('audio1')
audio.pause()
console.log(this.languageChoose)
}
}
}



