export default {
  data () {
    return {
      ck: false
    }
  },
  props: ['status'],
  methods: {
    ckchange (e) {
      if (this.status === 0) {
        e.preventDefault()
        this.ck = false
        this.$emit('clk', this.ck)
      }
    }
  },
  watch: {
    ck: function () {
      this.$emit('clk', this.ck)
    }
  }
}



